  mounted() {
    // 页面加载时先获取用户位置，然后获取月相信息
    this.initializeMoonPhaseData();
  },
  beforeDestroy() {
    // 清理缓存和定时器
    if (this.apiRequestManager.cache) {
      this.apiRequestManager.cache.clear();
    }
  },
  methods: {
    // 获取用户位置
    getUserLocation() {
      this.loadingMoonPhase = true;
      
      // 首先检查是否有缓存的位置信息
      const cachedLocation = this.getCachedData('userLocation');
      if (cachedLocation) {
        this.userCoordinates = cachedLocation.coordinates;
        this.userLocation = cachedLocation.location; // 同时设置位置名称
        console.log('使用缓存的用户位置:', this.userCoordinates, this.userLocation);
        this.getMoonPhaseInfo();
        return;
      }
      
      // 检查浏览器是否支持地理位置API
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          // 成功获取位置
          async (position) => {
            // 注意：经度在前，纬度在后，与API要求格式一致
            this.userCoordinates = `${position.coords.longitude},${position.coords.latitude}`;
            console.log('获取到用户GPS位置:', this.userCoordinates);
            
            // 通过坐标获取城市名称
            try {
              await this.getLocationNameFromCoordinates(position.coords.latitude, position.coords.longitude);
            } catch (error) {
              console.warn('获取城市名称失败，使用IP定位作为备选:', error);
              // 如果通过坐标获取城市名称失败，使用IP定位获取城市名称
              await this.getLocationFromIP();
              return;
            }
            
            // 缓存位置信息
            this.setCachedData('userLocation', {
              coordinates: this.userCoordinates,
              location: this.userLocation,
              timestamp: Date.now()
            });
            
            // 获取到位置后请求月相信息
            this.getMoonPhaseInfo();
          },
          // 获取位置失败
          (error) => {
            console.error('获取位置失败:', error.message);
            // 即使获取位置失败，也尝试获取月相信息（使用IP定位作为备选）
            this.getLocationFromIP();
          },
          // 选项
          {
            enableHighAccuracy: true, // 尝试获取最高精度的位置
            timeout: 5000,           // 5秒超时
            maximumAge: 300000       // 使用5分钟内的缓存位置
          }
        );
      } else {
        console.error('浏览器不支持地理位置API');
        // 如果浏览器不支持，使用IP定位
        this.getLocationFromIP();
      }
    },

    // 通过坐标获取城市名称（逆地理编码）
    async getLocationNameFromCoordinates(lat, lon) {
      try {
        console.log('通过坐标获取城市名称:', lat, lon);
        
        // 使用OpenStreetMap的Nominatim逆地理编码服务（免费）
        const response = await axios.get(`https://nominatim.openstreetmap.org/reverse?format=json&lat=${lat}&lon=${lon}&accept-language=zh-CN`, {
          headers: {
            'User-Agent': 'BiorhythmApp/1.0'
          }
        });
        
        if (response.data && response.data.address) {
          const address = response.data.address;
          const country = address.country || '中国';
          const state = address.state || address.province || '';
          const city = address.city || address.town || address.village || '';
          const district = address.district || address.suburb || '';
          
          // 构建位置名称
          let locationParts = [country];
          if (state) locationParts.push(state);
          if (city && city !== state) locationParts.push(city);
          if (district && district !== city) locationParts.push(district);
          
          this.userLocation = locationParts.join(' ');
          console.log('通过坐标获取到城市名称:', this.userLocation);
          return;
        }
        
        // 如果Nominatim API失败，尝试使用IP定位作为备选
        throw new Error('Nominatim逆地理编码失败');
        
      } catch (error) {
        console.warn('通过坐标获取城市名称失败，尝试IP定位:', error);
        
        // 备选方案：使用IP定位获取城市名称（但保留GPS坐标）
        try {
          const response = await axios.get('http://ip-api.com/json/?lang=zh-CN');
          
          if (response.data && response.data.status === 'success') {
            const city = response.data.city;
            const region = response.data.regionName;
            const country = response.data.country;
            
            this.userLocation = `${country} ${region} ${city}`;
            console.log('通过IP获取到城市名称:', this.userLocation);
            return;
          }
        } catch (ipError) {
          console.error('IP定位也失败:', ipError);
        }
        
        // 最终备选：使用默认位置名称
        this.userLocation = '中国';
        console.log('使用默认位置名称:', this.userLocation);
      }
    },

    // 通过IP获取位置信息
    async getLocationFromIP() {
      try {
        console.log('尝试通过IP获取位置');
        const response = await axios.get('http://ip-api.com/json/?lang=zh-CN');
        
        if (response.data && response.data.status === 'success') {
          const lat = response.data.lat;  // 纬度
          const lon = response.data.lon;  // 经度
          const city = response.data.city;
          const region = response.data.regionName;
          const country = response.data.country;
          
          // 构建位置名称和坐标
          this.userLocation = `${country} ${region} ${city}`;
          this.userCoordinates = `${lon},${lat}`;
          
          console.log('IP定位成功:', this.userLocation, this.userCoordinates);
          
          // 缓存位置信息
          this.setCachedData('userLocation', {
            coordinates: this.userCoordinates,
            location: this.userLocation,
            timestamp: Date.now()
          });
          
          // 获取月相信息
          this.getMoonPhaseInfo();
        } else {
          throw new Error('IP定位失败');
        }
      } catch (error) {
        console.error('IP定位失败:', error);
        // 使用默认位置（北京）
        this.userLocation = '北京';
        this.userCoordinates = '116.41,39.92';
        this.getMoonPhaseInfo();
      }
    },
    
    // 直接调用和风天气API获取月相信息
    async getMoonPhaseInfo() {
      // 检查请求频率限制
      if (!this.canMakeRequest()) {
        console.warn('请求过于频繁，已被限制');
        this.loadingMoonPhase = false;
        this.moonPhaseError = '请求过于频繁，请稍后再试';
        return;
      }

      // 检查缓存
      const cacheKey = this.getCacheKey();
      const cachedData = this.getCachedData(cacheKey);
      if (cachedData) {
        console.log('使用缓存的月相数据');
        this.updateMoonPhaseInfo(cachedData);
        this.loadingMoonPhase = false;
        return;
      }

      // 防止重复请求
      if (this.apiRequestManager.isRequesting) {
        console.log('请求正在进行中，跳过重复请求');
        return;
      }

      this.apiRequestManager.isRequesting = true;
      this.moonPhaseError = null;
      
      try {
        // 获取当前日期（中国时区）
        const now = new Date();
        const date = now.toISOString().slice(0, 10).replace(/-/g, '');
        
        // 和风天气API配置
        const apiKey = '7b93d00ce7dc403099ba16bc38728066';
        const coordinates = this.userCoordinates || '116.41,39.92'; // 默认北京
        
        // 构建请求URL
        const url = `https://nb564tjxuf.re.qweatherapi.com/v7/astronomy/moon?location=${coordinates}&date=${date}&key=${apiKey}`;
        
        console.log('请求月相API:', url);
        
        // 记录请求时间
        this.updateRequestCounter();
        
        const response = await axios.get(url, {
          timeout: 10000,
          headers: {
            'Accept': 'application/json',
            'Cache-Control': 'no-cache'
          }
        });
        
        if (response.data && response.data.code === '200') {
          const result = {
            success: true,
            location: this.userLocation || '北京',
            date: date,
            currentTime: now.toISOString(),
            moonrise: response.data.moonrise,
            moonset: response.data.moonset,
            moonPhaseListForDay: response.data.moonPhase || []
          };
          
          // 选择代表性月相
          const representativePhase = this.determineRepresentativeMoonPhase(result.moonPhaseListForDay);
          result.moonPhase = representativePhase.name;
          result.illumination = representativePhase.illumination;
          result.moonPhaseValue = representativePhase.value;
          
          // 缓存响应数据
          this.setCachedData(cacheKey, result);
          this.updateMoonPhaseInfo(result);
          
          console.log('月相信息获取成功:', result);
        } else {
          throw new Error(`API返回错误: ${response.data.code || '未知错误'}`);
        }
      } catch (error) {
        console.error('月相API请求失败:', error);
        if (error.code === 'ECONNABORTED') {
          this.moonPhaseError = '请求超时，请检查网络连接';
        } else if (error.response) {
          this.moonPhaseError = `API错误: ${error.response.status}`;
        } else {
          this.moonPhaseError = '网络连接失败，请稍后重试';
        }
        this.moonPhaseInfo.success = false;
      } finally {
        this.loadingMoonPhase = false;
        this.apiRequestManager.isRequesting = false;
      }
    },

    // 新增：请求频率控制
    canMakeRequest() {
      const now = Date.now();
      const timeSinceLastRequest = now - this.apiRequestManager.lastRequestTime;
      
      // 检查最小请求间隔
      if (timeSinceLastRequest < this.apiRequestManager.minRequestInterval) {
        return false;
      }
      
      // 检查每分钟请求次数限制
      const oneMinuteAgo = now - 60000;
      const recentRequests = this.apiRequestManager.requestCounter;
      
      if (recentRequests >= this.apiRequestManager.maxRequestsPerMinute) {
        return false;
      }
      
      return true;
    },

    // 新增：更新请求计数器
    updateRequestCounter() {
      const now = Date.now();
      this.apiRequestManager.lastRequestTime = now;
      this.apiRequestManager.requestCounter++;
      
      // 每分钟重置计数器
      setTimeout(() => {
        this.apiRequestManager.requestCounter = Math.max(0, this.apiRequestManager.requestCounter - 1);
      }, 60000);
    },

    // 新增：缓存管理
    getCacheKey() {
      const today = new Date().toDateString();
      const coordinates = this.userCoordinates || 'default';
      return `moonPhase_${today}_${coordinates}`;
    },

    getCachedData(key) {
      if (!this.apiRequestManager.cache.has(key)) {
        return null;
      }
      
      const cachedItem = this.apiRequestManager.cache.get(key);
      const now = Date.now();
      
      if (now - cachedItem.timestamp > this.apiRequestManager.cacheExpiry) {
        this.apiRequestManager.cache.delete(key);
        return null;
      }
      
      return cachedItem.data;
    },

    setCachedData(key, data) {
      this.apiRequestManager.cache.set(key, {
        data: data,
        timestamp: Date.now()
      });
      
      // 清理过期缓存
      this.cleanExpiredCache();
    },

    cleanExpiredCache() {
      const now = Date.now();
      for (const [key, item] of this.apiRequestManager.cache.entries()) {
        if (now - item.timestamp > this.apiRequestManager.cacheExpiry) {
          this.apiRequestManager.cache.delete(key);
        }
      }
    },

    // 新增：更新月相信息的统一方法
    updateMoonPhaseInfo(rawMoonData) {
      const dailyPhases = rawMoonData.moonPhaseListForDay; 

      if (dailyPhases && Array.isArray(dailyPhases) && dailyPhases.length > 0) {
        const representativePhase = this.determineRepresentativeMoonPhase(dailyPhases);
        
        this.moonPhaseInfo = {
          success: rawMoonData.success,
          location: rawMoonData.location,
          date: rawMoonData.date, 
          currentTime: rawMoonData.currentTime, 
          moonrise: rawMoonData.moonrise,
          moonset: rawMoonData.moonset,
          moonPhase: representativePhase.name,
          illumination: representativePhase.illumination,
          moonPhaseValue: representativePhase.value,
        };
      } else {
        console.warn("月相API响应中未找到 'moonPhaseListForDay' 数组，或数组为空。");
        if (rawMoonData.moonPhase) {
           this.moonPhaseInfo = {
              success: rawMoonData.success,
              location: rawMoonData.location,
              date: rawMoonData.date,
              currentTime: rawMoonData.currentTime,
              moonrise: rawMoonData.moonrise,
              moonset: rawMoonData.moonset,
              moonPhase: rawMoonData.moonPhase,
              illumination: rawMoonData.illumination,
              moonPhaseValue: rawMoonData.moonPhaseValue,
           };
        } else {
          this.moonPhaseError = '月相数据格式不正确或列表为空。';
          this.moonPhaseInfo.success = false; 
        }
      }
    },
    
    // 根据月相类型获取对应的图片
    getMoonPhaseImage() {
      if (!this.moonPhaseInfo.moonPhase) {
        return require('@/assets/img/moon-phases/full-moon.jpg'); // 默认图片
      }
      
      const phaseName = this.moonPhaseInfo.moonPhase.trim();
      
      // 精确匹配月相名称
      if (phaseName === '新月' || phaseName.includes('新月')) {
        return this.moonPhaseImages['新月'];
      } else if (phaseName === '娥眉月' || phaseName.includes('眉月')) {
        return this.moonPhaseImages['娥眉月'];
      } else if (phaseName === '上弦月' || phaseName.includes('上弦')) {
        return this.moonPhaseImages['上弦月'];
      } else if (phaseName === '盈凸月' || phaseName.includes('盈凸')) {
        return this.moonPhaseImages['盈凸月'];
      } else if (phaseName === '满月' || phaseName === '滿月' || 
                phaseName.includes('满月') || phaseName.includes('滿月')) {
        return this.moonPhaseImages['满月'];
      } else if (phaseName === '亏凸月' || phaseName === '虧凸月' || 
                phaseName.includes('亏凸') || phaseName.includes('虧凸')) {
        return this.moonPhaseImages['亏凸月'];
      } else if (phaseName === '下弦月' || phaseName.includes('下弦')) {
        return this.moonPhaseImages['下弦月'];
      } else if (phaseName === '残月' || phaseName === '殘月' || 
                phaseName.includes('残月') || phaseName.includes('殘月')) {
        return this.moonPhaseImages['残月'];
      }
      
      // 如果没有精确匹配，根据照明度返回最接近的月相图片
      const illumination = parseFloat(this.moonPhaseInfo.illumination) || 0;
      
      if (illumination >= 95) {
        return this.moonPhaseImages['满月'];
      } else if (illumination <= 5) {
        return this.moonPhaseImages['新月'];
      } else if (illumination < 25) {
        return this.moonPhaseImages['娥眉月']; 
      } else if (illumination < 50) {
        return this.moonPhaseImages['上弦月'];
      } else if (illumination < 75) {
        return this.moonPhaseImages['盈凸月'];
      } else { // illumination >= 75 and < 95
        return this.moonPhaseImages['亏凸月'];
      }
    },
    
    // 根据新的逻辑确定当天代表性的月相（与后端逻辑保持一致）
    determineRepresentativeMoonPhase(dailyPhases) {
      if (!dailyPhases || dailyPhases.length === 0) {
        console.warn('Daily moon phases list is empty or undefined.');
        return { name: '未知', illumination: '0', value: '0', fxTime: '' };
      }

      // 关键月相及其简体名称的映射 (优先级顺序)
      const KEY_MOON_PHASES_MAP = {
        '新月': ['新月'], 
        '满月': ['满月', '滿月'],
        '上弦月': ['上弦月'], 
        '下弦月': ['下弦月']  
      };
      
      // 保持优先级顺序的简体名称列表
      const KEY_MOON_PHASES_PRIORITY_SIMPLIFIED = ['新月', '满月', '上弦月', '下弦月'];

      let foundKeyPhase = null;

      // Step 1: 按优先级检查关键月相
      for (const simplifiedKeyPhaseName of KEY_MOON_PHASES_PRIORITY_SIMPLIFIED) {
        const possibleNames = KEY_MOON_PHASES_MAP[simplifiedKeyPhaseName] || [simplifiedKeyPhaseName];
        const phaseMatch = dailyPhases.find(p => p.name && possibleNames.includes(p.name.trim()));
        
        if (phaseMatch) {
          foundKeyPhase = { ...phaseMatch }; // 创建副本
          foundKeyPhase.name = simplifiedKeyPhaseName; // 标准化为简体名称
          break; // 找到最高优先级的关键月相
        }
      }

      if (foundKeyPhase) {
        return foundKeyPhase;
      }

      // Step 2: 如果没有关键月相，找持续时间最长的月相
      const phaseCounts = {};
      dailyPhases.forEach(phase => {
        if (phase.name) {
          const phaseNameTrimmed = phase.name.trim();
          phaseCounts[phaseNameTrimmed] = (phaseCounts[phaseNameTrimmed] || 0) + 1;
        }
      });

      let longestPhaseOriginalName = null;
      let maxCount = 0;
      for (const phaseNameKey in phaseCounts) {
        if (phaseCounts[phaseNameKey] > maxCount) {
          maxCount = phaseCounts[phaseNameKey];
          longestPhaseOriginalName = phaseNameKey;
        }
      }

      if (longestPhaseOriginalName) {
        const representative = dailyPhases.find(p => p.name && p.name.trim() === longestPhaseOriginalName);
        if (representative) {
          let finalName = representative.name.trim();
          // 尝试标准化名称
          for (const simpName in KEY_MOON_PHASES_MAP) {
            if (KEY_MOON_PHASES_MAP[simpName].includes(finalName)) {
              finalName = simpName;
              break;
            }
          }
          return { ...representative, name: finalName };
        }
        
        // 备选方案：使用第一个月相
        const firstPhaseFallback = { ...dailyPhases[0] };
        if (firstPhaseFallback.name) {
          let finalFirstNameFallback = firstPhaseFallback.name.trim();
          for (const simpName in KEY_MOON_PHASES_MAP) {
            if (KEY_MOON_PHASES_MAP[simpName].includes(finalFirstNameFallback)) {
              finalFirstNameFallback = simpName;
              break;
            }
          }
          firstPhaseFallback.name = finalFirstNameFallback;
        }
        return firstPhaseFallback; 
      }

      // 最终备选方案：返回列表中的第一个月相
      const firstPhase = { ...dailyPhases[0] };
      if (firstPhase.name) {
        let finalFirstName = firstPhase.name.trim();
        for (const simpName in KEY_MOON_PHASES_MAP) {
          if (KEY_MOON_PHASES_MAP[simpName].includes(finalFirstName)) {
            finalFirstName = simpName;
            break;
          }
        }
        firstPhase.name = finalFirstName;
      }
      return firstPhase;
    },

    // 获取月相图片的引用信息
    getMoonPhaseAttribution() {
      // 根据当前月相返回对应的引用信息
      const moonPhase = this.moonPhaseInfo.moonPhase;
      
      if (moonPhase.includes('娥眉') || moonPhase.includes('眉月')) {
