    calculateChineseZodiac(year) {
      const zodiacMap = ['猴', '鸡', '狗', '猪', '鼠', '牛', '虎', '兔', '龙', '蛇', '马', '羊'];
      // 中文生肖到英文图片文件名的映射
      const zodiacImageNameMap = {
        '鼠': 'Rat',
        '牛': 'Ox',
        '虎': 'Tiger',
        '兔': 'Rabbit',
        '龙': 'Dragon',
        '蛇': 'Snake',
        '马': 'Horse',
        '羊': 'Goat', // 或者 'Sheep'
        '猴': 'Monkey',
        '鸡': 'Rooster',
        '狗': 'Dog',
        '猪': 'Pig'
      };

      let index = (year - 1980) % 12;
      if (index < 0) {
        index += 12;
      }
      this.chineseZodiac = zodiacMap[index];
      
      const imageName = zodiacImageNameMap[this.chineseZodiac];

      if (imageName) {
        try {
          this.chineseZodiacImage = require(`@/assets/img/zodiac/${imageName}.jpeg`);
        } catch (e) {
          console.warn(`生肖图片 src/assets/img/zodiac/${imageName}.jpeg 未找到, 请添加图片。错误: ${e.message}`);
          this.chineseZodiacImage = ''; // 设置一个默认的空图片或者占位符图片
        }
      } else {
        console.warn(`未找到生肖 '${this.chineseZodiac}' 对应的图片文件名映射。`);
        this.chineseZodiacImage = ''; // 设置一个默认的空图片或者占位符图片
      }
    },
    
    // 格式化时间
    formatTime(timeString) {
      if (!timeString) return '未知';
      
      try {
        const date = new Date(timeString);
        return `${date.getHours().toString().padStart(2, '0')}:${date.getMinutes().toString().padStart(2, '0')}`;
      } catch (e) {
        return timeString;
      }
    },
    
    calculateBiorhythm() {
      if (!this.birthdate) {
        alert('请输入出生日期');
        return;
      }

      const birthDate = new Date(this.birthdate);
      const targetDate = new Date(this.targetDate);
      
      // 计算出生至今的天数
      const timeDiff = targetDate.getTime() - birthDate.getTime();
      this.daysSinceBirth = Math.floor(timeDiff / (1000 * 3600 * 24));
      
      // 计算年龄
      this.age = this.calculateAge(birthDate, targetDate);
      
      // 步骤2：计算各周期余数
      const physicalRemainder = this.daysSinceBirth % 23;
      const emotionalRemainder = this.daysSinceBirth % 28;
      const intellectualRemainder = this.daysSinceBirth % 33;
      
      // 步骤3：计算每日状态值（基础生物节律）
      const basePhysical = Math.sin(2 * Math.PI * physicalRemainder / 23);
      const baseEmotional = Math.sin(2 * Math.PI * emotionalRemainder / 28);
      const baseIntellectual = Math.sin(2 * Math.PI * intellectualRemainder / 33);

      // 获取月相影响系数
      const moonInfluence = this.getMoonPhaseInfluenceFactors();

      // 获取生肖五行影响系数
      const zodiacInfluence = this.getZodiacInfluenceFactors();

      // 应用月相和生肖影响并添加到数据数组
      let finalPhysical = this.applyMoonPhaseInfluence(basePhysical, moonInfluence.physical);
      finalPhysical = this.applyZodiacInfluence(finalPhysical, zodiacInfluence.physical);

      let finalEmotional = this.applyMoonPhaseInfluence(baseEmotional, moonInfluence.emotional);
      finalEmotional = this.applyZodiacInfluence(finalEmotional, zodiacInfluence.emotional);

      let finalIntellectual = this.applyMoonPhaseInfluence(baseIntellectual, moonInfluence.intellectual);
      finalIntellectual = this.applyZodiacInfluence(finalIntellectual, zodiacInfluence.intellectual);

      // 最终结果
      this.biorhythm.physical = Math.round(finalPhysical * 100) / 100;
      this.biorhythm.emotional = Math.round(finalEmotional * 100) / 100;
      this.biorhythm.intellectual = Math.round(finalIntellectual * 100) / 100;

      // 计算生肖
      this.calculateChineseZodiac(birthDate.getFullYear());
      
      this.showResults = true;
      
      // 在下一个DOM更新周期后绘制图表
      this.$nextTick(() => {
        this.drawChart();
      });
    },
    
    calculateAge(birthDate, currentDate) {
      let age = currentDate.getFullYear() - birthDate.getFullYear();
      const m = currentDate.getMonth() - birthDate.getMonth();
      
      if (m < 0 || (m === 0 && currentDate.getDate() < birthDate.getDate())) {
        age--;
      }
      
      return age;
    },
    
    formatDate(dateString) {
      const date = new Date(dateString);
      return `${date.getFullYear()}年${date.getMonth() + 1}月${date.getDate()}日`;
    },
    
    getProgressWidth(value) {
      // 将-1到1的值转换为0到100%的宽度
      return `${((value + 1) / 2 * 100).toFixed(0)}%`;
    },
    
    getProgressColor(value) {
      // 根据值的不同返回不同的颜色
      if (value > 0.5) return '#4CAF50'; // 高值 - 绿色
      if (value > 0) return '#8BC34A'; // 中高值 - 浅绿色
      if (value > -0.5) return '#FFC107'; // 中低值 - 黄色
      return '#F44336'; // 低值 - 红色
    },
    
    getPhysicalDescription() {
