      if (value > -0.3) return '您的情绪状态处于低潮期，可能有些波动，注意情绪管理。';
      if (value > -0.7) return '您的情绪状态较差，可能感到焦虑或不安，建议放松心情。';
      return '您的情绪状态处于低谷期，应避免重要的情感决策，多进行自我关怀。';
    },
    
    getIntellectualDescription() {
      const value = this.biorhythm.intellectual;
      
      // 判断是否为临界日
      if (this.isCriticalDay(value)) {
        return '今天是智力节律的临界日，思维可能不稳定，建议避免重要的决策和复杂思考。';
      }
      
      if (value > 0.7) return '您的智力状态极佳，思维敏捷，适合学习新知识和解决复杂问题。';
      if (value > 0.3) return '您的智力状态良好，逻辑思维清晰，适合进行分析和决策。';
      if (value > 0) return '您的智力状态处于高潮期，思维较为敏捷。';
      if (value > -0.3) return '您的智力状态处于低潮期，注意集中注意力，避免复杂的思维活动。';
      if (value > -0.7) return '您的智力状态较差，可能感到思维迟缓，建议简化工作内容。';
      return '您的智力状态处于低谷期，应避免重要的决策和复杂的思维任务。';
    },
    
    isCriticalDay(value) { 
      return Math.abs(value) < 0.1; 
    },
    
    drawChart() {
      const ctx = this.$refs.biorhythmChart.getContext('2d');
      
      // 如果已有图表，先销毁
      if (this.chart) {
        this.chart.destroy();
      }
      
      // 生成前后15天的数据
      const days = 31; // 总共显示31天
      const midPoint = Math.floor(days / 2); // 中间点
      
      const labels = [];
      const physicalData = [];
      const emotionalData = [];
      const intellectualData = [];
      
      const targetDateObj = new Date(this.targetDate);
      
      for (let i = 0; i < days; i++) {
        const currentDate = new Date(targetDateObj);
        currentDate.setDate(targetDateObj.getDate() - midPoint + i);
        
        // 添加日期标签
        labels.push(currentDate.getDate() + '日');
        
        // 计算当天的生物节律
        const dayDiff = Math.floor((currentDate - new Date(this.birthdate)) / (1000 * 3600 * 24));
        
        // 计算各周期余数
        const physicalRemainder = dayDiff % 23;
        const emotionalRemainder = dayDiff % 28;
        const intellectualRemainder = dayDiff % 33;
        
        // 计算状态值
        const basePhysical = Math.sin(2 * Math.PI * physicalRemainder / 23);
        const baseEmotional = Math.sin(2 * Math.PI * emotionalRemainder / 28);
        const baseIntellectual = Math.sin(2 * Math.PI * intellectualRemainder / 33);

        // 获取月相影响系数（图表中使用当前月相影响）
        const moonInfluence = this.getMoonPhaseInfluenceFactors();

        // 获取生肖五行影响系数
        const zodiacInfluence = this.getZodiacInfluenceFactors();

        // 应用月相和生肖影响并添加到数据数组
        let finalPhysical = this.applyMoonPhaseInfluence(basePhysical, moonInfluence.physical);
        finalPhysical = this.applyZodiacInfluence(finalPhysical, zodiacInfluence.physical);

        let finalEmotional = this.applyMoonPhaseInfluence(baseEmotional, moonInfluence.emotional);
        finalEmotional = this.applyZodiacInfluence(finalEmotional, zodiacInfluence.emotional);

        let finalIntellectual = this.applyMoonPhaseInfluence(baseIntellectual, moonInfluence.intellectual);
        finalIntellectual = this.applyZodiacInfluence(finalIntellectual, zodiacInfluence.intellectual);

        physicalData.push(Math.round(finalPhysical * 100) / 100);
        emotionalData.push(Math.round(finalEmotional * 100) / 100);
        intellectualData.push(Math.round(finalIntellectual * 100) / 100);
      }
      
      // 创建图表
      this.chart = new Chart(ctx, {
        type: 'line',
        data: {
          labels: labels,
          datasets: [
            {
              label: '身体节律',
              data: physicalData,
              borderColor: '#F44336',
              backgroundColor: 'rgba(244, 67, 54, 0.1)',
              tension: 0.4,
              borderWidth: 2,
              pointRadius: 3,
              pointBackgroundColor: '#F44336'
            },
            {
              label: '情感节律',
              data: emotionalData,
              borderColor: '#4CAF50',
              backgroundColor: 'rgba(76, 175, 80, 0.1)',
              tension: 0.4,
              borderWidth: 2,
              pointRadius: 3,
              pointBackgroundColor: '#4CAF50'
            },
            {
              label: '智力节律',
              data: intellectualData,
              borderColor: '#2196F3',
              backgroundColor: 'rgba(33, 150, 243, 0.1)',
              tension: 0.4,
              borderWidth: 2,
              pointRadius: 3,
              pointBackgroundColor: '#2196F3'
            }
          ]
        },
        options: {
          responsive: true,
          maintainAspectRatio: false,
          scales: {
            y: {
              min: -1,
              max: 1,
              ticks: {
                callback: function(value) {
                  if (value === 1) return '高峰';
                  if (value === 0) return '临界';
                  if (value === -1) return '低谷';
                  return '';
                }
              },
              grid: {
                color: function(context) {
                  if (context.tick.value === 0) {
                    return 'rgba(0, 0, 0, 0.5)';
                  }
                  return 'rgba(0, 0, 0, 0.1)';
                },
                lineWidth: function(context) {
                  if (context.tick.value === 0) {
                    return 2;
                  }
                  return 1;
                }
              }
            },
            x: {
              grid: {
                display: false
              }
            }
          },
          plugins: {
            tooltip: {
              mode: 'index',
              intersect: false,
              backgroundColor: 'rgba(0, 0, 0, 0.8)',
              titleColor: '#fff',
              bodyColor: '#fff',
              borderColor: '#ddd',
              borderWidth: 1,
              cornerRadius: 6,
              displayColors: true,
              callbacks: {
                title: function(tooltipItems) {
                  const index = tooltipItems[0].dataIndex;
                  const currentDate = new Date(targetDateObj);
                  currentDate.setDate(targetDateObj.getDate() - midPoint + index);
                  return currentDate.getFullYear() + '年' + 
                         (currentDate.getMonth() + 1) + '月' + 
                         currentDate.getDate() + '日';
                },
                label: function(context) {
                  let label = context.dataset.label || '';
                  if (label) {
                    label += ': ';
                  }
                  if (context.parsed.y !== null) {
                    // 确保使用四舍五入到小数点后两位
                    const value = Math.round(context.parsed.y * 100) / 100;
                    label += value.toFixed(2);
                  }
                  return label;
                }
              }
            },
            annotation: {
              annotations: {
                line1: {
                  type: 'line',
                  xMin: midPoint,
                  xMax: midPoint,
                  borderColor: 'rgba(0, 0, 0, 0.5)',
                  borderWidth: 2,
                  borderDash: [5, 5],
                  label: {
                    content: '今天',
                    enabled: true,
                    position: 'top'
                  }
                }
              }
            }
          }
        }
      });
    },
