        '马': ['#FF0000', '#FFFF00', '#32CD32'],
        '羊': ['#32CD32', '#8B4513', '#FFFF00'],
        '猴': ['#FFD700', '#FFFFFF', '#0066CC'],
        '鸡': ['#FFD700', '#8B4513', '#FFFFFF'],
        '狗': ['#8B4513', '#FF0000', '#32CD32'],
        '猪': ['#FFD700', '#8B4513', '#0066CC']
      };
      return colors[this.chineseZodiac] || ['#FFD700', '#FF0000', '#32CD32'];
    },
    
    getFortuneLevel() {
      const levels = {
        '鼠': 85, '牛': 70, '虎': 75, '兔': 65,
        '龙': 90, '蛇': 80, '马': 70, '羊': 60,
        '猴': 75, '鸡': 80, '狗': 65, '猪': 85
      };
      return levels[this.chineseZodiac] || 70;
    },
    
    getLoveLevel() {
      const levels = {
        '鼠': 70, '牛': 85, '虎': 60, '兔': 90,
        '龙': 75, '蛇': 70, '马': 65, '羊': 85,
        '猴': 80, '鸡': 65, '狗': 90, '猪': 80
      };
      return levels[this.chineseZodiac] || 70;
    },

    // 获取月相影响系数
    getMoonPhaseInfluenceFactors() {
      if (!this.enableMoonPhaseInfluence || !this.moonPhaseInfo.success) {
        return { emotional: 0, intellectual: 0, physical: 0 };
      }

      const currentMoonPhase = this.moonPhaseInfo.moonPhase;
      
      // 精确匹配月相名称
      for (const [phaseName, factors] of Object.entries(this.moonPhaseInfluence)) {
        if (currentMoonPhase.includes(phaseName) || phaseName.includes(currentMoonPhase)) {
          return factors;
        }
      }

      // 如果没有精确匹配，根据照明度估算月相影响
      const illumination = parseFloat(this.moonPhaseInfo.illumination) || 0;
      return this.estimateMoonPhaseInfluenceByIllumination(illumination);
    },

    // 根据照明度估算月相影响
    estimateMoonPhaseInfluenceByIllumination(illumination) {
      if (illumination <= 5) {
        // 新月
        return this.moonPhaseInfluence['新月'];
      } else if (illumination <= 25) {
        // 娥眉月
        return this.moonPhaseInfluence['娥眉月'];
      } else if (illumination <= 45) {
        // 上弦月
        return this.moonPhaseInfluence['上弦月'];
      } else if (illumination <= 75) {
        // 盈凸月
        return this.moonPhaseInfluence['盈凸月'];
      } else if (illumination >= 95) {
        // 满月
        return this.moonPhaseInfluence['满月'];
      } else if (illumination >= 75) {
        // 亏凸月
        return this.moonPhaseInfluence['亏凸月'];
      } else if (illumination >= 55) {
        // 下弦月
        return this.moonPhaseInfluence['下弦月'];
      } else {
        // 残月
        return this.moonPhaseInfluence['残月'];
      }
    },

    // 应用月相影响到生物节律计算
    applyMoonPhaseInfluence(baseValue, moonInfluence) {
      if (!this.enableMoonPhaseInfluence) {
        return baseValue;
      }
      
      // 使用加权平均的方式融合月相影响
      // 基础生物节律占75%，月相影响占15%，为生肖影响预留10%
      const baseWeight = 0.75;
      const moonWeight = this.moonPhaseWeight;
      
      // 月相影响值需要标准化到[-1, 1]范围
      const normalizedMoonInfluence = moonInfluence * 0.3; // 降低月相影响强度
      
      return baseWeight * baseValue + moonWeight * normalizedMoonInfluence;
    },

    // 获取月相影响说明
    getMoonPhaseInfluenceDescription() {
      if (!this.enableMoonPhaseInfluence || !this.moonPhaseInfo.success) {
        return '';
      }

      const moonPhase = this.moonPhaseInfo.moonPhase;
      const influences = this.getMoonPhaseInfluenceFactors();
      
      const descriptions = {
        '新月': '新月期间，月球引力最小，人体代谢放缓，体力和情绪处于低谷期，适合休息和内省。',
        '娥眉月': '娥眉月阶段，能量开始缓慢回升，但仍需谨慎安排高强度活动，适合制定计划。',
        '上弦月': '上弦月时期，交感神经活动趋于平衡，体力明显提升，逻辑思维能力增强。',
        '盈凸月': '盈凸月接近满月，各项能力接近峰值，创造力和执行力强，但需注意心血管压力。',
        '满月': '满月期间，月球引力最强，情绪高涨，记忆力和直觉达到峰值，但需注意情绪波动。',
        '亏凸月': '亏凸月阶段，能量开始下降，情绪可能波动，决策能力下降，需要适当休整。',
        '下弦月': '下弦月时期，副交感神经占主导，适合深度思考和复盘，体力趋于稳定。',
        '残月': '残月期间，处于能量低谷，情绪易焦虑，免疫力下降，应避免高强度任务。'
      };

      for (const [phaseName, desc] of Object.entries(descriptions)) {
        if (moonPhase.includes(phaseName) || phaseName.includes(moonPhase)) {
          return desc;
        }
      }

      return '当前月相对人体生物节律有一定影响，建议根据个人感受调整活动强度。';
    },

    // 获取生肖五行影响系数
    getZodiacInfluenceFactors() {
      if (!this.enableZodiacInfluence || !this.chineseZodiac) {
        return { emotional: 0, intellectual: 0, physical: 0 };
      }

      const zodiacInfo = this.zodiacElements[this.chineseZodiac];
      if (!zodiacInfo) {
        return { emotional: 0, intellectual: 0, physical: 0 };
      }

      const elementInfluence = this.elementInfluence[zodiacInfo.element];
      
      // 基础五行影响
      let influence = {
        physical: elementInfluence.physical,
        emotional: elementInfluence.emotional,
        intellectual: elementInfluence.intellectual
      };

      // 阴阳调节：阳性增强10%，阴性减弱5%
      const yinYangModifier = zodiacInfo.yin_yang === '阳' ? 1.1 : 0.95;
      influence.physical *= yinYangModifier;
      influence.emotional *= yinYangModifier;
      influence.intellectual *= yinYangModifier;

      // 检查是否有对冲影响（基于当前日期的年份生肖）
      const currentYear = new Date().getFullYear();
      const currentYearZodiac = this.calculateChineseZodiacByYear(currentYear);
      const conflictZodiac = this.zodiacConflicts[this.chineseZodiac];
      
      if (conflictZodiac === currentYearZodiac) {
        // 对冲年份，所有节律减弱15%
        influence.physical *= 0.85;
        influence.emotional *= 0.85;
        influence.intellectual *= 0.85;
      }

      // 标准化到[-1, 1]范围
      return {
        physical: (influence.physical - 0.6) * 2, // 将0.4-0.8范围映射到-0.4到0.4
        emotional: (influence.emotional - 0.6) * 2,
        intellectual: (influence.intellectual - 0.6) * 2
      };
    },

    // 根据年份计算生肖
    calculateChineseZodiacByYear(year) {
      const zodiacArray = ['猴', '鸡', '狗', '猪', '鼠', '牛', '虎', '兔', '龙', '蛇', '马', '羊'];
      let index = (year - 1980) % 12;
      if (index < 0) {
        index += 12;
      }
      return zodiacArray[index];
    },

    // 应用生肖影响到生物节律计算
    applyZodiacInfluence(baseValue, zodiacInfluence) {
      if (!this.enableZodiacInfluence) {
        return baseValue;
      }
      
      // 在已经融合月相影响的基础上，再融合生肖影响
      // 已融合月相的结果占90%，生肖影响占10%
      const baseWeight = 0.90;
      const zodiacWeight = this.zodiacWeight;
      
      // 生肖影响值需要标准化
      const normalizedZodiacInfluence = zodiacInfluence * 0.25; // 降低生肖影响强度
      
      return baseWeight * baseValue + zodiacWeight * normalizedZodiacInfluence;
    },

    // 获取生肖五行影响说明
    getZodiacInfluenceDescription() {
      if (!this.enableZodiacInfluence || !this.chineseZodiac) {
        return '';
      }

      const zodiacInfo = this.zodiacElements[this.chineseZodiac];
      if (!zodiacInfo) {
        return '';
      }

      const currentYear = new Date().getFullYear();
      const currentYearZodiac = this.calculateChineseZodiacByYear(currentYear);
      const conflictZodiac = this.zodiacConflicts[this.chineseZodiac];
      
      let description = `您的生肖${this.chineseZodiac}属${zodiacInfo.element}${zodiacInfo.yin_yang}，`;
      
      // 五行特性描述
      const elementDescriptions = {
        '木': '木主生长发育，增强身体节律，体力充沛，适合运动和体力活动',
        '火': '火主热情活力，强化情感节律，情绪丰富，社交能力强',
        '土': '土主稳重踏实，各项节律平衡发展，性格稳定可靠',
        '金': '金主收敛内敛，增强智力节律，思维敏锐，逻辑性强',
        '水': '水主智慧灵活，全面提升各项节律，适应能力强'
      };
      
      description += elementDescriptions[zodiacInfo.element] + '。';
      
      // 阴阳影响描述
      if (zodiacInfo.yin_yang === '阳') {
        description += '阳性特质使您的各项能力更加活跃积极。';
      } else {
        description += '阴性特质使您的能力发展更加内敛稳定。';
      }
      
      // 对冲影响描述
      if (conflictZodiac === currentYearZodiac) {
        description += `今年是${currentYearZodiac}年，与您的生肖${this.chineseZodiac}形成对冲，各项节律可能受到一定影响，建议多加注意身心调节。`;
      }
      
      return description;
    },

    // 太极八卦显示器相关方法
    getRhythmAngle(value) {
      // 将-1到1的值映射到0-360度
      // -1对应180度（下方），0对应270度（左方），1对应0度（上方）
      return ((value + 1) * 180) % 360;
    },

    getHarmonyLevel() {
      // 计算三个节律的和谐度（越接近0越和谐）
      const avg = (Math.abs(this.biorhythm.physical) + 
                   Math.abs(this.biorhythm.emotional) + 
                   Math.abs(this.biorhythm.intellectual)) / 3;
      return Math.round((1 - avg) * 100);
    },

    toggleChartView() {
      this.showTraditionalChart = !this.showTraditionalChart;
      if (this.showTraditionalChart) {
        // 延迟绘制图表，确保DOM已更新
        this.$nextTick(() => {
          this.drawChart();
        });
      }
    },

    // 生肖相关方法
  }
}
